import 'bootstrap/dist/css/bootstrap.min.css';
import React, {useState} from "react";
import "./App.css";
import Header from "./components/Header";

import {Player} from "./Player";
import InputForm from "./components/InputForm";
import { Button, Col, Container, Row } from "react-bootstrap";
import AnswerForm from "./components/AnswerForm";

import {Notes} from "./Notes";
import {Utils} from "./Utils";
import {Config} from "./Config";

function App() {
  
  let numberOfNotes = Config.initialNumberOfNotes;
  let rootNote = Notes.getNotes()[0];
  let interval = Notes.intervals[0][0];

  const [generatedNotes, setGeneratedNotes] = useState<string[]>([])

  function setInputs(rNote: string, intervalParam: string, numNotes: string) {
    numberOfNotes = parseInt(numNotes);
    rootNote = rNote;
    interval = intervalParam;
  }

 

  const generateNotes = () => {
    Utils.debugLog(`Generating for rootNode: ${rootNote} interval: ${interval}, numberOfNotes: ${numberOfNotes}`)
    const notes = Notes.generateNotes(rootNote,interval,numberOfNotes);
    setGeneratedNotes(notes);
    Utils.debugLog("Notes: ");
    Utils.debugLog(notes);
    Player.playNotes(notes);
  };

  return (
    <>
      <Header />
      <Container>
        <Row>
          <Col>
            <InputForm  setInputs={setInputs}  />
          </Col>
        </Row>
        <Row>
          <Col>
            <Button className="mt-3" onClick={generateNotes}>
              Generate Notes
            </Button>
          </Col>
        </Row>
        <Row>
          <Col>
            <AnswerForm notes={generatedNotes} />
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default App;
