import * as Tone from "tone";
import {Config} from './Config'

export class Player {
  static synth: Tone.Synth<Tone.SynthOptions>;

  public static playNotes(notes: string[]) {
    if (!Player.synth) {
      Player.synth = new Tone.Synth().toDestination();
    }
    let playAt = Tone.now();
    for (var note of notes) {
      Player.synth.triggerAttackRelease(note, "8n", playAt);
      playAt += Config.noteDuration;
    }
  }
}
