import {Config} from './Config'
import {Utils} from './Utils'


export class Notes {
    static readonly rawNotes = ["C","C#","D","D#","E","F","F#","G","G#","A","b","B"];
    static notes:string[];
    static readonly intervals:[string,number] [] = [
        ["M2",2],
        ["M3",4],
        ["P4",5],
        ["P5",7],
        ["M6",9],
        ["M7",11],
        ["Octave",12],
    ];

    public static getNotes(){
        Notes.ensureNotes();
        return Notes.notes;
    }

    private static ensureNotes(){
        if(Notes.notes){
            return Notes.notes;
        }
        Notes.notes = Notes.rawNotes.map(x=> Notes.addOctaveToRawNote(x,Config.startingOctave) );
    }

    private static addOctaveToRawNote(rawNote:string, octave:number){
        if(rawNote.length===1){
            return rawNote+octave;
        }
        return rawNote.slice(0,1)+octave+rawNote.slice(1);
    }

    public static generateNotes(rootNote:string, interval:string, numberOfNotes: number){
        const notes = Array.from(Notes.getAllPossibleNotes(rootNote,interval));
        if(notes.length===0){
            return [];
        }
        const result:string[]=[];
        for(let i = 0; i < numberOfNotes; i++){
            const rndIdx = Math.floor(Math.random() * notes.length);
            result.push(notes[rndIdx]);
        }
        return result;
    }

    public static getAllPossibleNotes(rootNote:string, interval:string){
        Notes.ensureNotes();
        const notes = new Set<string>();
        const noteIdx = Notes.notes.findIndex((e)=>e===rootNote);
        if(noteIdx <0){
            Utils.debugLog("Unexpected: Could not find note: " + rootNote);
            return notes;
        }
        const distanceIdx = Notes.intervals.findIndex((e)=>e[0]===interval);
        if(distanceIdx <0){
            Utils.debugLog("Unexpected: Could not find interval: " + interval);
            return notes;
        }

        const maximumDistance = Notes.intervals[distanceIdx][1];

        for (let i = 0; i <= distanceIdx; i++) {
            const possibleNotes = Notes.getPossibleNotes(noteIdx,maximumDistance, Notes.intervals[i][1] );
            possibleNotes.forEach(k=>notes.add(k));
        }
        return notes;
    }

    private static getPossibleNotes(startIdx: number, maximumDistance:number, stepSize: number){
        const allowedNotes: string[] = [];
        let distance=0;
        while(distance<=maximumDistance){
            const idx =  Math.trunc((startIdx+distance)%Notes.rawNotes.length);
            const octaveIdx = Math.trunc((startIdx+distance)/Notes.rawNotes.length);
            allowedNotes.push( Notes.addOctaveToRawNote(Notes.rawNotes[idx],Config.startingOctave+octaveIdx) );
            distance += stepSize;
        }
        return allowedNotes;
    }


}